@import url(https://fonts.googleapis.com/css?family=Dosis|PT+Sans+Narrow&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bellota&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 5px 20px;
}

h2 {
    color: rgb(190, 92, 199);
    font-family: 'Bellota', cursive;
    text-align: center;
    text-decoration: underline;
    text-shadow: 1px 1px 1px rgb(66, 66, 66),
                       3px 3px 8px rgb(199, 195, 195);
  }

h3 {
  color: rgb(84, 18, 122);
  text-align: center;
  padding-top: 10px;
  font-family: 'Bellota', cursive;
  padding-bottom: 20px;
  text-shadow: 1px 1px 1px rgb(219, 213, 213),
                       3px 3px 8px rgb(199, 195, 195);
}

p {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 20px;
  padding: 5px;
  text-indent: 60px;
  text-align: justify;
  line-height: 1.5;
  color: rgb(244, 223, 250);
  text-shadow: 1px 1px 1px rgb(66, 66, 66),
                       3px 3px 8px black;
                       
}

.center {
    text-align: center center;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 200px;
    width: 150px;
    float: left;
    padding: 5px;
    padding-left: 20vh;
}

header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100vh;
    height: 38vh;
    padding-left: 10px;
    padding-right: 10px;
    border: 2px solid rgb(165, 102, 165);
    border-radius: 10px;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);

}

body {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100vh;
    height: 28vh;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    background-color: rgb(211, 177, 219);
}

footer {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100vh;
    height: 35vh;
    padding-left: 10px;
    padding-right: 10px;
    border: 2px solid rgb(165, 102, 165);
    border-radius: 10px;
}

